
import path from 'path'
import { computed, defineComponent, getCurrentInstance, nextTick, onBeforeMount, reactive, ref, watch, toRefs } from 'vue'
import ScrollPane from './ScrollPane.vue'
import { visitedViewsTypes } from '@/store/modules/tagViews/state'
import { TagsActionTypes } from '@/store/modules/tagViews/action-types'
import { useStore } from '@/store'
import { RouteRecordRaw, useRoute, useRouter } from 'vue-router'

export default defineComponent({
  components: {
    ScrollPane
  },
  setup() {
    const store = useStore()
    const router = useRouter()
    const instance = getCurrentInstance()
    const currentRoute = useRoute()
    const scrollPaneRef = ref(null)

    const toLastView = (visitedViews: visitedViewsTypes[], view: visitedViewsTypes) => {
      const latestView = visitedViews.slice(-1)[0]
      if (latestView !== undefined && latestView.fullPath !== undefined) {
        router.push(latestView.fullPath).catch(err => {
          console.warn(err)
        })
      } else {
        if (view.name === 'Home') {
        // to reload home page
          router.push({ path: '/redirect' + view.fullPath }).catch(err => {
            console.warn(err)
          })
        } else {
          router.push('/').catch(err => {
            console.warn(err)
          })
        }
      }
    }

    const state = reactive({
      visible: false,
      top: 0,
      left: 0,
      selectedTag: {} as visitedViewsTypes,
      affixTags: [] as visitedViewsTypes[],
      isActive: (route: visitedViewsTypes) => {
        return route.path === currentRoute.path
      },
      isAffix: (tag: visitedViewsTypes) => {
        return tag.meta && tag.meta.affix
      },
      refreshSelectedTag: (view: visitedViewsTypes) => {
        store.dispatch(TagsActionTypes.ACTION_DEL_CACHED_VIEW, view)
        const { fullPath } = view
        nextTick(() => {
          router.replace({ path: '/redirect' + fullPath }).catch(err => {
            console.warn(err)
          })
        })
      },
      // 关闭选中的 tag 标签
      closeSelectedTag: (view: visitedViewsTypes) => {
        store.dispatch(TagsActionTypes.ACTION_DEL_VIEW, view)
        if (state.isActive(view)) {
          toLastView(store.state.tagViews.visitedViews, view)
        }
      },
      // 关闭其他的 tag 标签
      closeOthersTags: () => {
        if (state.selectedTag.fullPath !== currentRoute.path && state.selectedTag.fullPath !== undefined) {
          router.push(state.selectedTag.fullPath).catch(err => {
            console.log(err)
          })
        }
        store.dispatch(TagsActionTypes.ACTION_DEL_OTHER_VIEW, state.selectedTag as visitedViewsTypes)
      },
      // 关闭全部的 tag 标签
      closeAllTags: (view: visitedViewsTypes) => {
        store.dispatch(TagsActionTypes.ACTION_DEL_ALL_VIEWS, undefined)
        if (state.affixTags.some(tag => tag.path === currentRoute.path)) {
          return
        }
        toLastView(store.state.tagViews.visitedViews, view)
      },
      openMenu: (tag: visitedViewsTypes, e: MouseEvent) => {
        // const menuMinWidth = 105
        // const offsetLeft = ctx.$el.getBoundingClientRect().left // container margin left
        // const offsetWidth = ctx.$el.offsetWidth // container width
        // const maxLeft = offsetWidth - menuMinWidth // left boundary
        // const left = e.clientX - offsetLeft + 15 // 15: margin right
        // if (left > maxLeft) {
        //   state.left = maxLeft
        // } else {
        //   state.left = left
        // }
        state.left = e.clientX
        state.top = e.clientY
        state.visible = true
        state.selectedTag = tag
      },
      closeMenu: () => {
        state.visible = false
      },
      handleScroll: () => {
        state.closeMenu()
      }
    })

    const visitedViews = computed(() => {
      return store.state.tagViews.visitedViews
    })
    const routes = computed(() => store.state.permission.routes)

    const filterAffixTags = (routes: RouteRecordRaw[], basePath = '/') => {
      let tags: visitedViewsTypes[] = []

      routes && routes.forEach(route => {
        if (route.meta && route.meta.affix) {
          const tagPath = path.resolve(basePath, route.path)
          tags.push({
            fullPath: tagPath,
            path: tagPath,
            name: route.name,
            meta: { ...route.meta }
          })
        }

        if (route.children) {
          const childTags = filterAffixTags(route.children, route.path)
          if (childTags.length >= 1) {
            tags = tags.concat(childTags)
          }
        }
      })
      return tags
    }
    const initTags = () => {
      state.affixTags = filterAffixTags(routes.value)
      for (const tag of state.affixTags) {
        // Must have tag name
        if (tag.name) {
          store.dispatch(TagsActionTypes.ACTION_ADD_VISITED_VIEW, tag as visitedViewsTypes)
        }
      }
    }

    const addTags = () => {
      if (currentRoute.name) {
        store.dispatch(TagsActionTypes.ACTION_ADD_VIEW, currentRoute)
      }
      return false
    }

    const addDefault = () => {
      // store.dispatch(TagsActionTypes.ACTION_ADD_VIEW, 'Home')
    }

    const moveToCurrentTag = () => {
      const tags = instance?.refs.tag as any[]
      nextTick(() => {
        if (tags === null || tags === undefined || !Array.isArray(tags)) { return }
        for (const tag of tags) {
          if ((tag.to as visitedViewsTypes).path === currentRoute.path) {
            (scrollPaneRef.value as any).moveToCurrentTag(tag)
            // When query is different then update
            if ((tag.to as visitedViewsTypes).fullPath !== currentRoute.fullPath) {
              store.dispatch(TagsActionTypes.ACTION_UPDATE_VISITED_VIEW, currentRoute)
            }
          }
        }
      })
    }

    watch(() => currentRoute.name, () => {
      if (currentRoute.name !== 'Login') {
        addTags()
        moveToCurrentTag()
      }
    })

    watch(() => state.visible, (value) => {
      if (value) {
        document.body.addEventListener('click', state.closeMenu)
      } else {
        document.body.removeEventListener('click', state.closeMenu)
      }
    })

    onBeforeMount(() => {
      addDefault()
      initTags()
      addTags()
    })
    return {
      visitedViews,
      routes,
      scrollPaneRef,
      ...toRefs(state)
    }
  }
})
